
@font-face {
  font-family: 'Droid Serif';
  src: url("~fonts/DroidSerif-Italic.woff") format('woff'), url("~fonts/DroidSerif-Italic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto Black';
  src: url("~fonts/Roboto-Black.woff") format('woff'), url("~fonts/Roboto-Black.ttf") format('truetype');
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto';
  src: url("~fonts/Roboto-Bold.woff") format('woff'), url("~fonts/Roboto-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: 'Roboto Light';
  src: url("~fonts/Roboto-Light.woff") format('woff'), url("~fonts/Roboto-Light.ttf") format('truetype');
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: 'Roboto Medium';
  src: url("~fonts/Roboto-MediumItalic.woff") format('woff'), url("~fonts/Roboto-MediumItalic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
.Project {
  overflow: hidden;
}
@media only screen and (min-width: 900px) {
.Project.active {
    position: fixed;
    z-index: 30;
    top: 0;
    left: 0;
}
}
.Project .splash {
  width: 100vw;
  height: 100vh;
  background-position: 50% 50%;
  background-size: cover;
  position: absolute;
  z-index: 25;
}
.Project .splash-leave-active {
  animation: splashToGallery 0.8s cubic-bezier(0.165, 0.84, 0.44, 1), galleryToHide 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) 0.8s forwards;
}
.Project .splash-enter-active {
  animation: hideToGallery 0.5s cubic-bezier(0.165, 0.84, 0.44, 1), galleryToSplash 0.8s cubic-bezier(0.165, 0.84, 0.44, 1) 0.5s forwards;
}
.Project .splash:after {
  content: '';
  position: absolute;
  width: 100vw;
  height: 100vh;
  background: url("../assets/trame.png");
  z-index: 0;
  opacity: 0.5;
}
.Project .splash header {
  position: absolute;
  transform-style: preserve-3d;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  z-index: 2;
}
.Project .splash header > * {
  transition: all 0.8s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.Project .splash header h1 {
  font-family: 'Roboto Black', sans-serif;
  font-weight: 400;
  letter-spacing: -0.02em;
  font-size: 10rem;
  margin: 0 0 5rem;
}
.Project .splash header h1 .by {
  display: block;
  font-size: 3rem;
  margin-top: 2rem;
  text-transform: uppercase;
}
.Project .splash header button {
  position: relative;
  font-family: 'Roboto Light', sans-serif;
  font-weight: 200;
  border: 2px solid #fff;
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  color: #fff;
  font-size: 2rem;
  text-transform: uppercase;
  cursor: pointer;
  transform: translateZ(0);
}
.Project .splash header button:after {
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  width: 10rem;
  height: 10rem;
  border-radius: 10rem;
  background-color: #fff;
  transform: scale(0);
  opacity: 0;
  transition: transform 0.3s cubic-bezier(0.19, 1, 0.22, 1), opacity 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  z-index: -1;
}
.Project .splash header button:hover {
  color: #1a1a1a;
}
.Project .splash header button:hover:after {
  opacity: 1;
  transform: scale(1);
}
.Project .splash.splash-leave-active header [data-t],
.Project .splash.splash-enter-active header [data-t] {
  opacity: 0;
}
.Project .splash.splash-leave-active header [data-t]:nth-child(1),
.Project .splash.splash-enter-active header [data-t]:nth-child(1) {
  transform: translate3d(-50px, 0, 0);
}
.Project .splash.splash-leave-active header [data-t]:nth-child(2),
.Project .splash.splash-enter-active header [data-t]:nth-child(2) {
  transform: translate3d(-100px, 0, 0);
}
.Project .splash.splash-leave-active header [data-t]:nth-child(3),
.Project .splash.splash-enter-active header [data-t]:nth-child(3) {
  transform: translate3d(-150px, 0, 0);
}
.Project .splash.splash-leave-active header [data-t]:nth-child(4),
.Project .splash.splash-enter-active header [data-t]:nth-child(4) {
  transform: translate3d(-200px, 0, 0);
}
.Project .splash.splash-leave-active header [data-t]:nth-child(5),
.Project .splash.splash-enter-active header [data-t]:nth-child(5) {
  transform: translate3d(-250px, 0, 0);
}
.Project aside {
  width: 50vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  background-size: cover;
  background-position: 50% 50%;
}
.Project aside:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(54,56,57,0.6);
  z-index: -1;
}
.Project aside .Gallery {
  width: 54vw;
  height: 60vh;
  position: absolute;
  top: 20vh;
  left: 4vw;
}
.Project aside .Close {
  position: absolute;
  top: 5vh;
  left: 4vw;
}
.Project article {
  position: absolute;
  width: 50vw;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 20vh 8vw 0 16vw;
  background-color: #1a1a1a;
  color: #fff;
  top: 0;
  right: 0;
}
@media only screen and (min-width: 900px) {
.Project article [data-t] {
    opacity: 0;
    transition: all 0.8s;
}
.Project article [data-t="1"] {
    transform: translate3d(50px, 0, 0);
}
.Project article [data-t="2"] {
    transform: translate3d(100px, 0, 0);
}
.Project article [data-t="3"] {
    transform: translate3d(150px, 0, 0);
}
.Project article [data-t="4"] {
    transform: translate3d(200px, 0, 0);
}
.Project article [data-t="5"] {
    transform: translate3d(250px, 0, 0);
}
.Project article [data-t="6"] {
    transform: translate3d(300px, 0, 0);
}
.Project article [data-t="7"] {
    transform: translate3d(350px, 0, 0);
}
.Project article [data-t="8"] {
    transform: translate3d(400px, 0, 0);
}
.Project article [data-t="9"] {
    transform: translate3d(450px, 0, 0);
}
.Project.active article [data-t] {
    transition-delay: 0.6s;
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
.Project article header p {
  margin-top: 0;
  font-size: 2rem;
}
.Project article header p.brand {
  font-family: 'Roboto Black', sans-serif;
  font-weight: 400;
  letter-spacing: -0.02em;
  font-size: 2em;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 0;
}
.Project article header p.by {
  font-family: Roboto, sans-serif;
  font-weight: 700;
  color: #1a1a1a;
  font-size: 1.8rem;
}
.Project article header p.by em {
  font-family: 'Droid Serif';
  font-style: italic;
  font-weight: 400;
}
.Project article header h1 {
  font-family: 'Roboto Medium', sans-serif;
  font-style: italic;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 5.8rem;
  margin: 0.3rem;
}
.Project article .content p {
  font-family: 'Roboto Light', sans-serif;
  font-weight: 200;
  font-size: 1.8em;
  line-height: 3rem;
}
.Project article footer .icon {
  width: 8rem;
  height: 4rem;
  fill: #fff;
  margin: 0 1rem;
}
@media only screen and (max-width: 900px) {
.Project {
    height: auto;
    position: relative;
}
.Project .splash {
    display: none;
}
.Project aside {
    height: calc(100vw / 16 * 9);
}
.Project article {
    height: auto;
    padding: 8vw;
}
.Project aside,
  .Project article {
    width: 100vw;
    min-height: 30vh;
    position: relative;
    display: block;
}
.Project aside .Gallery {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
}
@keyframes splashToGallery {
from {
    -webkit-clip-path: polygon(0 0, 100vw 0, 100vw 100vh, 0 100vh, 0 0);
            clip-path: polygon(0 0, 100vw 0, 100vw 100vh, 0 100vh, 0 0);
}
to {
    -webkit-clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
}
}
@keyframes galleryToSplash {
from {
    -webkit-clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
}
to {
    -webkit-clip-path: polygon(0 0, 100vw 0, 100vw 100vh, 0 100vh, 0 0);
            clip-path: polygon(0 0, 100vw 0, 100vw 100vh, 0 100vh, 0 0);
}
}
@keyframes galleryToHide {
from {
    -webkit-clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
}
to {
    -webkit-clip-path: polygon(4vw 20vh, 4vw 20vh, 4vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 4vw 20vh, 4vw 80vh, 4vw 80vh, 4vw 20vh);
}
}
@keyframes hideToGallery {
from {
    -webkit-clip-path: polygon(4vw 20vh, 4vw 20vh, 4vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 4vw 20vh, 4vw 80vh, 4vw 80vh, 4vw 20vh);
}
to {
    -webkit-clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
            clip-path: polygon(4vw 20vh, 58vw 20vh, 58vw 80vh, 4vw 80vh, 4vw 20vh);
}
}
aside .Gallery nav {
  bottom: auto;
  top: calc(100% + 10vh);
  right: calc(8vw + 4vw);
  transform: translate(0, -50%);
}
article footer {
  width: 38vw;
  position: absolute;
  display: block;
  top: 10vh;
  right: 58vw;
  z-index: 5;
  text-align: right;
}
article footer a {
  opacity: 0.3;
  transition: opacity 0.3s ease-out;
  transform: translateZ(0);
  display: inline-block;
  will-change: opacity;
  overflow: hidden;
}
article footer a:hover {
  opacity: 1;
}


