
@font-face {
  font-family: 'Droid Serif';
  src: url("~fonts/DroidSerif-Italic.woff") format('woff'), url("~fonts/DroidSerif-Italic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto Black';
  src: url("~fonts/Roboto-Black.woff") format('woff'), url("~fonts/Roboto-Black.ttf") format('truetype');
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto';
  src: url("~fonts/Roboto-Bold.woff") format('woff'), url("~fonts/Roboto-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: 'Roboto Light';
  src: url("~fonts/Roboto-Light.woff") format('woff'), url("~fonts/Roboto-Light.ttf") format('truetype');
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: 'Roboto Medium';
  src: url("~fonts/Roboto-MediumItalic.woff") format('woff'), url("~fonts/Roboto-MediumItalic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
@import "../node_modules/normalize.css/normalize.css";
html {
  font-size: 62.5%;
}
body {
  position: absolute;
  min-width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
}
@media only screen and (max-width: 900px) {
body {
    overflow-y: inherit !important;
}
}
#app {
  width: 100vw;
  height: 100vh;
}
::-webkit-scrollbar {
  position: absolute;
  width: 1rem;
}
::-webkit-scrollbar-track {
  opacity: 0.2;
  background: rgba(26,26,26,0.1);
}
::-webkit-scrollbar-thumb {
  background: rgba(26,26,26,0.3);
}
a {
  color: #1a1a1a;
}
a[href^="https://twitter"] {
  color: #1da1f2;
}
button {
  background: transparent;
}
button:active,
button:focus {
  outline: none;
}
section {
  width: 100vw;
  height: 100vh;
  background: #363839;
  position: relative;
}


