
@font-face {
  font-family: 'Droid Serif';
  src: url("~fonts/DroidSerif-Italic.woff") format('woff'), url("~fonts/DroidSerif-Italic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto Black';
  src: url("~fonts/Roboto-Black.woff") format('woff'), url("~fonts/Roboto-Black.ttf") format('truetype');
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto';
  src: url("~fonts/Roboto-Bold.woff") format('woff'), url("~fonts/Roboto-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: 'Roboto Light';
  src: url("~fonts/Roboto-Light.woff") format('woff'), url("~fonts/Roboto-Light.ttf") format('truetype');
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: 'Roboto Medium';
  src: url("~fonts/Roboto-MediumItalic.woff") format('woff'), url("~fonts/Roboto-MediumItalic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
section.SectionHome {
/*background url(https://squidpress.files.wordpress.com/2008/10/dsc_0006.jpg)*/
  background: #0091d9;
  background-size: cover;
  background-position: 50% 50%;
}
section.SectionHome header {
  position: absolute;
  transform-style: preserve-3d;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  text-align: center;
  z-index: 5;
}
section.SectionHome header h1 {
  margin: 0 0 3rem;
  font-family: 'Roboto Black', sans-serif;
  font-weight: 400;
  letter-spacing: -0.02em;
  font-size: 6em;
  line-height: 4.8rem;
  text-transform: uppercase;
  white-space: nowrap;
}
section.SectionHome header p {
  font-size: 4em;
  margin: 0;
  font-family: 'Droid Serif';
  font-style: italic;
  font-weight: 400;
}
section.SectionHome header .links {
  margin-top: 10rem;
}
section.SectionHome header .icon {
  fill: #fff;
  width: 3.5rem;
  height: 3.5rem;
  margin: 0 0.5rem;
  opacity: 0.5;
  transition: opacity 0.2s ease-out;
}
section.SectionHome header .icon:hover {
  opacity: 1;
}
section.SectionHome nav {
  position: fixed;
  top: 5rem;
  left: calc(100vw - 5rem);
  transform: translateX(-100%);
  white-space: nowrap;
  z-index: 100;
}
section.SectionHome button {
  font-size: 1.6rem;
  text-transform: uppercase;
  color: #fff;
  border: 1px solid #fff;
  border-radius: 0.5rem;
  padding: 1rem 1.7rem;
  opacity: 0.3;
  cursor: pointer;
  transform: scale(1);
  transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
}
section.SectionHome button:hover {
  opacity: 1;
}
section.SectionHome button.scaleup-enter-active,
section.SectionHome button.scaleup-leave-active {
  opacity: 0;
  transform: scale(0);
}
section.SectionHome canvas {
  position: absolute;
  z-index: 1;
  cursor: none;
}


