
@font-face {
  font-family: 'Droid Serif';
  src: url("~fonts/DroidSerif-Italic.woff") format('woff'), url("~fonts/DroidSerif-Italic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto Black';
  src: url("~fonts/Roboto-Black.woff") format('woff'), url("~fonts/Roboto-Black.ttf") format('truetype');
  font-style: normal;
  font-weight: 400;
}
@font-face {
  font-family: 'Roboto';
  src: url("~fonts/Roboto-Bold.woff") format('woff'), url("~fonts/Roboto-Bold.ttf") format('truetype');
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: 'Roboto Light';
  src: url("~fonts/Roboto-Light.woff") format('woff'), url("~fonts/Roboto-Light.ttf") format('truetype');
  font-style: normal;
  font-weight: 200;
}
@font-face {
  font-family: 'Roboto Medium';
  src: url("~fonts/Roboto-MediumItalic.woff") format('woff'), url("~fonts/Roboto-MediumItalic.ttf") format('truetype');
  font-style: italic;
  font-weight: 400;
}
.Gallery {
  background: #363839;
}
.Gallery .inner {
  width: 100%;
  height: 100%;
  position: relative;
/*overflow hidden*/
}
.Gallery img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  -webkit-clip-path: clip-path polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 0);
          clip-path: clip-path polygon(0 0, 100% 0, 100% 100%, 0 100%, 0 0);
  transform: translateZ(0);
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  z-index: 0;
}
.Gallery img.left {
  z-index: 1;
  animation: clipLeft 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}
.Gallery img.right {
  z-index: 1;
  animation: clipRight 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
}
.Gallery nav {
  position: absolute;
  bottom: 4rem;
  right: 4rem;
  z-index: 5;
}
.Gallery nav button:last-child {
  margin-left: 2rem;
}
@keyframes clipLeft {
0% {
    -webkit-clip-path: polygon(0 0, 200% 0, 100% 100%, 0 100%, 0 0);
            clip-path: polygon(0 0, 200% 0, 100% 100%, 0 100%, 0 0);
}
100% {
    -webkit-clip-path: polygon(0 0, 0 0, 0 100%, 0 100%, 0 0);
            clip-path: polygon(0 0, 0 0, 0 100%, 0 100%, 0 0);
}
}
@keyframes clipRight {
0% {
    -webkit-clip-path: polygon(-100% 0, 100% 0, 100% 100%, 0 100%, -100% 0);
            clip-path: polygon(-100% 0, 100% 0, 100% 100%, 0 100%, -100% 0);
}
100% {
    -webkit-clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%, 100% 0);
            clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%, 100% 0);
}
}


